<template>
	<view class="_content" style="padding: 0rpx 20rpx; ">
		<view class="_content">
			<!-- 筛选条件 -->
			<view class="card">
				<uni-row>
					<view class="uni-padding-wrap uni-common-mt" style="height: 70rpx;margin-top: 5rpx;">
						<uni-segmented-control :current="current" size="200px" :values="items" :style-type="styleType"
							:active-color="activeColor" @clickItem="onClickItem" />
					</view>
				</uni-row>
				<uni-row class="uni-row">
					<uni-col style="height: 80rpx;" :span="6">
						<view class="uni-col_search">模糊查询:</view>
					</uni-col>
					<uni-col style="height: 80rpx;" :span="18" class="divRight">
						<u--input placeholder="请输入内容" border="surround" clearable v-model="billNo"></u--input>
					</uni-col>
				</uni-row>
				<uni-row class="uni-row">
					<uni-col style="height: 80rpx;" class="custom-col" :span="6">
						<view class="uni-col_search">供应商:</view>
					</uni-col>
					<uni-col style="height: 80rpx;" :span="18" class="divRight custom-col">
						<view class="uni-col-view" @click="drawerOpen1"
							style="width: 100%; border: 1rpx solid #bdc3c7;padding-left: 9rpx;">{{ selectedItem1.cVenName || '请点击选择'
							}} </view>
					</uni-col>
				</uni-row>
				<uni-row class="uni-row">
					<uni-col style="height: 80rpx;" class="custom-col" :span="6">
						<view class="uni-col_search">产品:</view>
					</uni-col>
					<uni-col style="height: 80rpx;" :span="18" class="divRight custom-col">
						<view class="uni-col-view" @click="drawerOpen"
							style="width: 100%; border: 1rpx solid #bdc3c7;padding-left: 9rpx;">{{ selectedItem.cInvName
								|| '请点击选择'
							}} </view>
					</uni-col>
				</uni-row>
				<uni-row class="uni-row">
					<uni-col style="height: 80rpx;" class="custom-col" :span="6">
						<view class="uni-col_search">时间:</view>
					</uni-col>
					<uni-col style="height: 80rpx;" :span="18" class="divRight custom-col">
						<uni-datetime-picker v-model="dateTime" type="datetimerange" rangeSeparator="至"
							@change="dateChange" />
					</uni-col>
				</uni-row>
				<uni-row class="uni-row">
					<uni-col :span="12">
						<view
							style="height: 70rpx; display: flex; justify-content: space-between; align-items: center;">
							<view style="width: 30%;color: white;"></view>
							<u-button size="small" text="重置" color="#bdc3c7" @click="rest"></u-button>
							<view style="width: 30%;color: white;"></view>
						</view>
					</uni-col>
					<uni-col :span="12">
						<view
							style="height: 70rpx; display: flex; justify-content: space-between; align-items: center;">
							<view style="width: 30%;color: white;"></view>
							<u-button size="small" type="primary" text="查询" @click="search"></u-button>
							<view style="width: 30%;color: white;"></view>
						</view>
					</uni-col>
				</uni-row>
			</view>
			<!-- 列表内容 -->
			<view v-if="this.listData.length !== 0">
				<view v-for="(item, index) in listData" :key="item.IDs" class="card">
					<uni-row class="uni-row firstrow">
						<uni-col :span="10">
							<view class="uni-col number">{{ index + 1 }}</view>
						</uni-col>
						<uni-col :span="14" class="divRight">
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col textStyle">{{ item.cPOID || item.DocCode }}</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.dPODate }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col">行号:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.irowno || item.ROWNO }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col">业务类型:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.cBusType }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col">供应商:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.cVenName }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col">存货编码:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="textContent">{{ item.cInvCode }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col">存货名称:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="textContent">{{ item.cInvName }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row" v-if="item.cInvAddCode">
						<uni-col :span="10">
							<view class="uni-col">存货代码:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.cInvAddCode }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col">规格型号:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.cInvStd }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row" v-if="item.cUnitID">
						<uni-col :span="10">
							<view class="uni-col">单位:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.cUnitID }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col">订单数量:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.iQuantity }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col">计划到货日期:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.dArriveDate }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col">入库数量:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.dhQuantity || item.iQuantity }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col">未入库数量:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.wrkQty }}</view>
						</uni-col>
					</uni-row>
					<uni-row class="uni-row">
						<uni-col :span="10">
							<view class="uni-col">未发数量:</view>
						</uni-col>
						<uni-col :span="14" class="textRight">
							<view class="uni-col">{{ item.wQuantity }}</view>
						</uni-col>
					</uni-row>
				</view>
				<!-- 加载状态提示 -->
				<view class="load-more">
					<text v-if="loading">加载中...</text>
					<text v-else-if="noMoreData">没有更多数据了</text>
					<text v-else="this.listData.length > 10">上拉加载更多</text>
				</view>
			</view>
		</view>

		<!-- 产品选择器 -->
		<u-popup  overlay="false" :show="drawerRight" closeable="true"
			@close="drawerClose" mode="bottom" :round="10">
			<view class="_container">
				<!-- 搜索区域 -->
				<view class="search-header">
					<view class="search-box">
						<view style="width: 80%;">
							<u--input placeholder="请输入物料编码或名称" border="surround" clearable v-model="searchInput"
								suffixIcon="search" iconStyle="color: #909399;" class="search-input"
								@input="getInvList"></u--input>
						</view>
						<view style="width: 20%;">
							<u-button text="重置" @click="restSelectItem" class="action-btn"></u-button>
						</view>
					</view>
				</view>

				<!-- 查询结果显示区域 -->
				<view class="result-list" v-if="invList.length">
					<view v-for="(item, index) in invList" :key="index"
						:class="['result-item', { 'selected': selectedItem.cInvCode === item.cInvCode }]"
						@click="selectItem(item)">
						<text>{{ item.cInvCode }} - {{ item.cInvName }}</text>
					</view>
				</view>
				<view v-else class="no-data">
					<text>没有找到数据</text>
				</view>
			</view>
		</u-popup>
		<!-- 供应商选择器 -->
		<u-popup :customStyle="select_popupStyle" overlay="false" :show="drawerRight1" closeable="true"
			@close="drawerClose1" mode="bottom" :round="10">
			<view class="_container">
				<!-- 搜索区域 -->
				<view class="search-header">
					<view class="search-box">
						<view style="width: 80%;">
							<u--input placeholder="请输入供应商编码或名称" border="surround" clearable v-model="searchInput1"
								suffixIcon="search" iconStyle="color: #909399;" class="search-input"
								@input="getVenList"></u--input>
						</view>
						<view style="width: 20%;">
							<u-button text="重置" @click="restSelectItem1" class="action-btn"></u-button>
						</view>
					</view>
				</view>

				<!-- 查询结果显示区域 -->
				<view class="result-list" v-if="venList.length">
					<view v-for="(item, index) in venList" :key="index"
						:class="['result-item', { 'selected': selectedItem1.cVenCode === item.cVenCode }]"
						@click="selectItem1(item)">
						<text>{{ item.cVenCode }} - {{ item.cVenName }}</text>
					</view>
				</view>
				<view v-else class="no-data">
					<text>没有找到数据</text>
				</view>
			</view>
		</u-popup>

		<!-- 占位块 -->
		<view style="height: 30rpx;"></view>
		<!-- 动画 -->
		<view v-if="this.listData.length == 0">
			<u-empty :mode="'data'" :icon="'https://cdn.uviewui.com/uview/demo/empty/data.png'">
			</u-empty>
		</view>
		<!-- <view v-if="nodata" class="empty">
			<div class="loader">
				<span class="loader-text">NoData...</span>
			</div>
		</view> -->
		<u-overlay :show="loding">
			<view class="warp">
				<view class="rect">
					<div class="dot-spinner">
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
					</div>
				</view>
			</view>
		</u-overlay>
	</view>
</template>

<script>
import UniRow from '../../components/uni-row/uni-row'
import { orderList, invList, venList } from "@/nxTemp/apis/sunset.js"
export default {
	components: { UniRow },
	data() {
		return {
			select_popupStyle: {
				width: '100vw',
				// height: '200rpx',
				backgroundColor: '#ffffff', // 添加背景色
				// borderRadius: '20rpx', // 添加圆角
			},

			currentPage: 1,
			pageSize: 20,
			loading: false,
			noMoreData: false,

			dateTime: [],
			startTime: '',
			endTime: '',
			billNo: '',
			cVenCode: '',
			cInvCode: '',

			venList: [],
			invList: [],
			searchInput: '', //物料查找关键字
			selectedItem: null,  // 存储选中的产品数据
			searchInput1: '', //供应商查找关键字
			selectedItem1: null,  // 存储选中的供应商数据
			drawerRight: false,	//查询物料
			drawerRight1: false,	//查询供应商

			userInfo: [],
			items: ['采购订单', '委外订单', '工序加工单'],
			current: 0,
			colorIndex: 0,
			activeColor: '#43C6AC',
			styleType: 'button',

			loding: false,
			type: '普通采购',

			nodata: false,

			listData: [
				// { "ID": "1002195645", "POID": 1002195645, "cBusType": "代管采购", "cPOID": "CG25010639", "dPODate": "2025-01-21", "cVenCode": "04043", "cVenName": "常州苏特轴承", "cMemo": null }, { "ID": "1002195646", "POID": "1002195646", "cBusType": "普通采购", "cPOID": "CG25010639", "dPODate": "2025-01-21", "cVenCode": "04043", "cVenName": "常州苏特轴承1", "cMemo": null }, { "ID": "1002195647", "POID": "1002195647", "cBusType": "代管采购", "cPOID": "CG25010639", "dPODate": "2025-01-21", "cVenCode": "04043", "cVenName": "常州苏特轴承2", "cMemo": null }, { "ID": "1002195648", "POID": "1002195648", "cBusType": "代管采购", "cPOID": "CG25010639", "dPODate": "2025-01-21", "cVenCode": "04043", "cVenName": "常州苏特轴承2", "cMemo": null }, { "ID": "1002195649", "POID": "1002195649", "cBusType": "代管采购", "cPOID": "CG25010639", "dPODate": "2025-01-21", "cVenCode": "04043", "cVenName": "常州苏特轴承2", "cMemo": null }, { "ID": "1002195650", "POID": "1002195651", "cBusType": "代管采购", "cPOID": "CG25010639", "dPODate": "2025-01-21", "cVenCode": "04043", "cVenName": "常州苏特轴承2", "cMemo": null }, { "ID": "1002195652", "POID": "1002195652", "cBusType": "代管采购", "cPOID": "CG25010639", "dPODate": "2025-01-21", "cVenCode": "04043", "cVenName": "常州苏特轴承2", "cMemo": null }, { "ID": "1002195653", "POID": "10021956453", "cBusType": "代管采购", "cPOID": "CG25010639", "dPODate": "2025-01-21", "cVenCode": "04043", "cVenName": "常州苏特轴承2", "cMemo": null }
			],
		};
	},
	onLoad() {
		uni.getStorage({
			key: 'userData',
			success: (res) => {
				console.log(JSON.stringify(res.data))
				this.userInfo = res.data; // 将用户数据赋值到当前页面的数据对象
			},
			fail: () => {
				uni.showToast({
					title: '用户数据读取失败，请重新登录',
					icon: 'none'
				});
				uni.reLaunch({
					url: '/pages/login/login'
				});
			}
		});

		setTimeout(() => {
			this.loding = true;
			this.getList();
			this.getInvList('');
			this.getVenList('');
		}, 1000);

	},
	methods: {
		restSelectItem() {
			this.selectedItem1 = null;
			this.cVenCode = ''; // 清空 cInvCode
		},
		// 选中物料项
		selectItem(item) {
			this.selectedItem = this.selectedItem === item ? null : item; // 切换选中状态
			if (this.selectedItem !== null) {
				this.cInvCode = item.cInvCode; // 更新 cInvCode
			} else {
				this.cInvCode = ''; // 清空 cInvCode
			}
			this.drawerRight = false; // 假设这是其他操作
		},
		drawerOpen() {
			this.drawerRight = true;
			console.log(this.drawerRight)
		},
		drawerClose() {
			this.drawerRight = false;
		},

		// 选中供应商项
		selectItem1(item) {
			this.selectedItem1 = this.selectedItem1 === item ? null : item; // 切换选中状态
			if (this.selectedItem1 !== null) {
				this.cVenCode = item.cVenCode; // 更新 cInvCode
			} else {
				this.cVenCode = ''; // 清空 cInvCode
			}
			this.drawerRight1 = false; // 假设这是其他操作
		},
		drawerOpen1() {
			this.drawerRight1 = true;
		},
		drawerClose1() {
			this.drawerRight1 = false;
		},
		restSelectItem1() {
			this.selectedItem1 = null;
			this.cVenCode = ''; // 清空 cInvCode
		},

		comboxInv(inputValue) {
			this.getInvList(inputValue);
		},

		comboxVen(inputValue) {
			this.getVenList(inputValue);
		},

		//默认时间(未使用)
		getDefaultDateRange() {
			const today = new Date();
			const startOfDay = new Date(today.setHours(0, 0, 0, 0)); // 今天0点
			const yesterday = new Date(startOfDay);
			yesterday.setDate(yesterday.getDate() - 1); // 前一天0点
			// 格式化为 ISO 字符串（例如：2025-02-07T00:00:00.000Z）
			return [yesterday.toISOString(), startOfDay.toISOString()];
		},
		//时间条件
		dateChange(newDatetime) {
			if (newDatetime && newDatetime.length == 2) {
				this.startTime = newDatetime[0];
				this.endTime = newDatetime[1];
			} else {
				this.startTime = '';
				this.endTime = '';
			}
		},
		//查询
		async search() {
			this.listData = []; // 清空列表数据
			this.loding = true;
			this.noMoreData = false;
			await this.getList(); // 调用获取数据的方法
		},
		//重置
		rest() {
			this.selectedItem1 = null;
			this.selectedItem = null; 
			this.startTime = ''
			this.endTime = ''
			this.billNo = ''
			this.cVenCode = ''
			this.cInvCode = ''
		},
		//分页
		async onClickItem(e) {
			if (this.current !== e.currentIndex) {
				this.current = e.currentIndex;
			}
			if (e.currentIndex == 0) {
				this.type = '普通采购'
			} else if (e.currentIndex == 1) {
				this.type = '委外加工'
			} else if (e.currentIndex == 2) {
				this.type = '工序委外'
			}
			this.getInvList();
			this.getVenList();
			this.listData = []
			this.loding = true;
			this.getList();
		},

		//订单数据获取
		async getList() {
			this.loading = true;

			const cVenCode = this.cVenCode || '';
			const cInvCode = this.cInvCode || '';
			const billNo = this.billNo || '';
			const startTime = this.startTime || '';
			const endTime = this.endTime || '';

			try {
				const res = await orderList({
					UserId: this.userInfo.UserId,
					OnlineCode: this.userInfo.OnlineCode,
					cBusType: this.type,
					page: this.currentPage,
					pageSize: this.pageSize,
					// 其他参数...
					BillNo: billNo, //模糊查询值
					cVenCode: cVenCode, //供应商
					cInvCode: cInvCode, //物料
					StartTime: startTime,
					EndTime: endTime,
				});

				if (res.success) {
					if (res.subdata.length > 0) {
						this.listData = this.listData.concat(res.subdata);
						this.currentPage++;
					} else {
						this.noMoreData = true;
					}
					//控制数据为空时的动画显示
					this.nodata = this.listData.length === 0;
				} else {
					uni.showToast({ title: res.message, icon: 'none' });
				}
			} catch (error) {
				uni.showToast({ title: '网络请求失败', icon: 'none' });
			} finally {
				this.loading = false;
				this.loding = false;
			}
		},

		//产品下拉树
		async getInvList(cInvCode) {
			let type = '';
			if (this.type == '普通采购') {
				type = '普通采购'
			} else if(this.type == '委外加工'){
				type = '委外加工'
			}else{
				type = '工序委外'
			}

			const res = await invList({
				UserId: this.userInfo.UserId,
				OnlineCode: this.userInfo.OnlineCode,
				cBusType: type,
				page: "1",
				pagesize: "100",
				cInvCode: cInvCode
			});

			if (res.success) {
				this.invList = res.subdata;
			}else {
				this.invList = []
			}
		},
		//供应商下拉树
		async getVenList(cVenCode) {
			let type = '';
			if (this.type == '普通采购') {
				type = '普通采购'
			} else if(this.type == '委外加工'){
				type = '委外加工'
			}else{
				type = '工序委外'
			}

			const res = await venList({
				UserId: this.userInfo.UserId,
				OnlineCode: this.userInfo.OnlineCode,
				cBusType: type,
				page: "1",
				pagesize: "100",
				cVenCode: cVenCode
			});

			if (res.success) {
				this.venList = res.subdata;
			}else{
				this.venList = []
			}
		}
	},
	// 上拉加载更多
	onReachBottom() {
		if (this.listData.length) {
			this.getList();
		}
	}
};
</script>


